(function(){
  'use strict';
    angular.module('gardeningApp')
      .controller('reminderCtrl', Remind)
      .directive('myModal', function() {
         return {
           restrict: 'A',
           scope: {
             close: '='
           },
           link: function(scope, element, attr) {
             scope.close = function() {
                 element.modal('hide');
             };
           }
         }
      });

    function Remind($http, appUrl, lang, toastr, $scope, $state){
      var remind = this;
      remind.lists = [];
      remind.modalclose = null;
      remind.loading= true;
      remind.disableInfiniteScroll = false;
      var count = 0;
      remind.getList = GetList.bind(remind);
      remind.submit = Post.bind(remind);

      remind.goToDone = function(){
        $state.go('reminder-done');
      }

      function GetList(){
        if (!remind.disableInfiniteScroll){
          remind.disableInfiniteScroll = true;
          $http.get(appUrl + 'reminders?page=' + count)
            .then(function(result){
              if (result.data.data.next_page_url == null){
                remind.disableInfiniteScroll = true;
              }else{

                count++;
                remind.disableInfiniteScroll = false;
                // GetList();
              }
              remind.loading = false;
              remind.lists = remind.lists.concat(result.data.data.data);


            })
        }
      }

      function Post(data){
        if (data){
          remind.submitting = true;
          $http.post(appUrl + 'reminder', {title: data}).then(function(Data){
            if (Data.status == 200){
              remind.modalclose();
              remind.lists.unshift({title: data, status: 0})
              toastr.info(lang.get().add_success);
            }
            remind.submitting = false;
            remind.newText = '';
          }, function(err){
            remind.submitting = false;
          })
        }
      }
      remind.getList();
    }

})();

(function(){
  'use strict';
    angular.module('gardeningApp')
      .controller('reminderDoneCtrl', Controller);

    function Controller(appUrl, $http){
      var done = this;
      done.loading = true;
      done.disableInfiniteScroll = false;
      done.lists = [];
      var q= '';
      var count = 0;
      var hasCount = false;
      done.infiniteLoading = true;
      done.oldTerm = '';
      done.getList = Getlist.bind();
      done.search = Getlist.bind();

      function Getlist(){
        if (!done.disableInfiniteScroll){
          done.loading = true;
          done.disableInfiniteScroll = true;
          if (done.searchTerm && (done.oldTerm != done.searchTerm)){
            done.oldTerm = JSON.parse(JSON.stringify(done.searchTerm));
            count = 0;
            q = done.searchTerm;
            done.lists = [];
          }else{
            q = ''
          }
          $http.get(appUrl + 'reminders/done?page=' + count+ '&q='+ q).success(function(result){
            console.log(result);
            if (result.data.next_page_url){
              count++
              done.disableInfiniteScroll = false;
            }else{
              done.disableInfiniteScroll = true;
            }
            done.lists = done.lists.concat(result.data.data);
            done.loading = false;

          }).error(function(err){
            done.loading = false;
          })
        }
      }

      done.getList();
    }

})();

(function() {
  'use strict';

  angular
    .module('gardeningApp')
    .controller('MainController', MainController);

  /** @ngInject */
  function MainController($timeout, webDevTec, toastr, $state) {
    var vm = this;

    vm.awesomeThings = [];
    vm.classAnimation = '';
    vm.creationDate = 1458543673656;
    vm.showToastr = showToastr;
    vm.trigger = Trigger.bind(vm);
    vm.goToReminder = function(){
      $state.go('reminder');
    }
    activate();

    function activate() {
      getWebDevTec();
      $timeout(function() {
        vm.classAnimation = 'rubberBand';
      }, 4000);
    }

    function Trigger(type){
      $state.go(type);
    }

    function showToastr() {
      toastr.info('Fork <a href="https://github.com/Swiip/generator-gulp-angular" target="_blank"><b>generator-gulp-angular</b></a>');
      vm.classAnimation = '';
    }

    function getWebDevTec() {
      vm.awesomeThings = webDevTec.getTec();

      angular.forEach(vm.awesomeThings, function(awesomeThing) {
        awesomeThing.rank = Math.random();
      });
    }
  }
})();

